.library-content-top {
  text-align: center;
}
.library-content-top img {
  padding: 15px;
  width: 100px;
  height: 100px;
  vertical-align: middle;
}
.library-content-top h1 {
  display: inline;
  vertical-align: middle;
}
.library-content-images {
  padding: 0;
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
}
.library-content-images li {
  padding: 15px;
  width: 25%;
  list-style: none;
}
.library-content-images img {
  width: 100%;
  cursor: pointer;
}
.library-content-item.active img {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  max-width:80vw;
  max-height: 80vh;
  object-fit: contain;
  z-index: 4;
}
.mask {
  transition-duration: 0.3s;
  opacity: 0;
}
.mask.active {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 3;
}
.library-content-videos video {
  width: 25%;
  padding: 15px;
}

@media (min-width: 640px) and (max-width: 1000px) {
  .library-content-images li {
    width: 33%;
  }
  .library-content-videos video {
    width: 33%;
  }
}

@media (max-width: 640px) {
  .library-content-images li {
    width: 50%;
  }
  .library-content-videos video {
    width: 50%;
  }
  .library-content-top img {
    width: 60px;
    height: 60px;
    padding: 10px;
  }
  .library-content-top h1 {
    font-size: 20px;
  }
  .library-content-images {
    margin: 0;
  }
}