.footer {
  padding: 0 30px;
  display: flex;
  justify-content: space-between;
}
.footer.page0 {
  background: #5febbc;
}
.footer.page1 {
  background: rgb(226, 99, 216);
}
.footer.page2 {
  background: rgb(49, 130, 197);
}
.footer.page3 {
  background: rgb(211, 211, 83);
}
.footer.page4 {
  background: rgb(74, 173, 74);
}
.footerLeft {
  padding: 10px 0;
}
.footerLeft h1 {
  color: #fff;
  display: inline;
  vertical-align: middle;
  font-size: 25px;
}
.footerLeft img {
  width: 40px;
  height: 40px;
  vertical-align: middle;
  margin-right: 10px;
}
.footerRight ul {
  margin: 10px 0;
}
.footerRight li {
  list-style-type: none;
}
.footerRight h1 {
  color: #fff;
  font-size: 20px;
  display: inline;
  vertical-align: middle;
}
.footerRight img {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  vertical-align: middle;
}
@media (max-width: 640px) {
  .footer {
    flex-direction: column;
  }
  .footerLeft h1 {
    font-size: 20px;
  }
  .footerRight {
    margin-bottom: 20px;
  }
  .footerRight ul {
    margin: 0;
    padding: 0 10px;
  }
  .footerRight img {
    width: 25px;
    height: 25px;
  }
  .footerRight h1 {
    font-size: 16px;
  }
}