.aboutus-text {
  text-align: center;
  background: rgba(0, 0, 0, 0.1);
  padding: 20px;
  color: #333;
}
.aboutus-text h1 {
  font-size: 36px;
}
.aboutus-text p {
  font-size: 20px;
}
.aboutus-image ul {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 0;
}
.aboutus-image li {
  box-sizing: border-box;
  padding: 20px;
  text-align: center;
  list-style: none;
  width: 30%;
}
.aboutus-image img {
  width: 100%;
  cursor: pointer;
}
.aboutus-image-item.active img {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  max-width: 80vw;
  max-height: 80vh;
  object-fit: contain;
  z-index: 4;
}
.mask {
  transition-duration: 0.3s;
  opacity: 0;
}
.mask.active {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 3;
}

@media (max-width: 640px) {
  .aboutus-text h1 {
    font-size: 24px;
  }
  .aboutus-text p {
    font-size: 20px;
  }
  .aboutus-image li {
    width: 50%;
  }
}