.top-image h1 {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  font-size: 30px;
  width: fit-content;
  padding: 0 10px;
  border-radius: 4px;
  background: rgba(255, 255, 255, 0.3);
}
.top-links {
  display: flex;
  flex-wrap: wrap;
  padding: 0 50px;
}
.top-link img {
  width: 50px;
  height: 50px;
}
.top-link h1 {
  margin: 0;
  font-size: 24px;
}
.top-link {
  position: relative;
  box-sizing: border-box;
  padding: 10px 0;
  color: #333;
  text-decoration: none;
  text-align: center;
  width: 25%;
  transition-duration: 0.5s;
}
.top-link:not(:last-of-type)::before{
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 70%;
  transform: translateY(-50%);
  border-right: solid 1px #333;
}
.top-link img, .top-link h1 {
  transition-duration: 0.5s;
}
.top-link.active {
  background: rgba(0, 0, 0, 0.1);
}
.top-link.active img {
  transform: scale(1.2) rotate(360deg);
}
.top-link.active h1 {
  opacity: 0.7;
}
.top-latest {
  text-align: center;
  padding-top: 30px;
}
.swiper-container {
  z-index: 0;
  width: 90%;
  padding: 30px 0;
}
.swiper-slide {
  text-align: center;
}
.swiper-slide img {
  width: 90%;
  object-fit: contain;
}
.tweet {
  text-align: center;
}
@media (max-width: 640px) {
  .top-links {
    padding: 10px 20px;
  }
  .top-image.top h1 {
    font-size: 16px;
    width: 90%;
  }
  .top-link {
    width: 50%;
  }
  .top-link:not(:last-of-type)::before {
    border: none;
  }
  .top-link:nth-of-type(even)::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 70%;
    transform: translateY(-50%);
    border-left: solid 1px #333;
  }
  .top-latest h1 {
    font-size: 28px;
  }
}