.forms {
  text-align: center;
  padding: 20px 0;
}
.formButtons button{
  margin-right: 20px;
}
.submitButton, .deleteButton {
  font-weight: bold;
  background: rgb(48, 135, 216);
  color: #fff;
  border: none;
  padding: 5px 10px;
  margin: 10px 10px;
  border-radius: 4px;
  transition-duration: 0.5s;
  cursor: pointer;
  border: solid rgb(48, 135, 216) 1px;
}
.submitButton:hover, .deleteButton:hover {
  background: #fff;
  color: rgb(48, 135, 216);
  border: solid rgb(48, 135, 216) 1px;
}

@media (max-width: 640px) {
  .formItem input {
    max-width: 60%;
  }
  .formItem textarea {
    max-width: 90%;
  }
}