.header {
  position: fixed;
  width: 100vw;
  height: 70px;
  top: 0;
  left: 0;
  z-index: 1;
}
.header.page0 {
  background: #5febbc;
}
.header.page1 {
  background: rgb(226, 99, 216);
}
.header.page2 {
  background: rgb(49, 130, 197);
}
.header.page3 {
  background: rgb(211, 211, 83);
}
.header.page4 {
  background: rgb(74, 173, 74);
}
.header img {
  width: 50px;
  height: 50px;
  margin: 0 10px;
  vertical-align: middle;
}
.header h1 {
  display: inline;
  margin: 10px 0;
  color: #fff;
  vertical-align: middle;
  line-height: 2;
}
.fas {
  color: #fff;
  position: fixed;
  right: 20px;
  top: 20px;
  font-size: 30px;
  transition-duration: 0.3s;
  z-index: 1;
  cursor: pointer;
}
.linkButton {
  display: flex;
  position: fixed;
  top: 70px;
  transform: translateY(-50px);
  visibility: hidden;
  opacity: 0;
  transition-duration: 0.3s;
  z-index: 1;
}
.linkButton.active{
  transform: translateY(0);
  visibility: visible;
  opacity: 1;
}
.links {
  box-sizing: border-box;
  text-decoration: none;
  width: 20vw;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  padding: 10px;
  transition-duration: 0.3s;
  opacity: 0.9;
}
.links.page0 {
  background: #5febbc;
}
.links.page1 {
  background: rgb(226, 99, 216);
}
.links.page2 {
  background: rgb(49, 130, 197);
}
.links.page3 {
  background: rgb(211, 211, 83);
}
.links.page4 {
  background: rgb(74, 173, 74);
}
.links.active {
  background: #fff;
}
.links.active.page0 {
  color: #5febbc;
  border: solid 2px #5febbc;
}
.links.active.page1 {
  color: rgb(226, 99, 216);
  border: solid 2px rgb(226, 99, 216);
}
.links.active.page2 {
  color: rgb(49, 130, 197);
  border: solid 2px rgb(49, 130, 197);
}
.links.active.page3 {
  color: rgb(211, 211, 83);
  border: solid 2px rgb(211, 211, 83);
}
.links.active.page4 {
  color: rgb(74, 173, 74);
  border: solid 2px rgb(74, 173, 74);
}
.links:hover {
  background: #fff;
}
.links.page0:hover {
  color: #5febbc;
}
.links.page1:hover {
  color: rgb(226, 99, 216);
}
.links.page2:hover {
  color: rgb(49, 130, 197);
}
.links.page3:hover {
  color: rgb(211, 211, 83);
}
.links.page4:hover {
  color: rgb(74, 173, 74);
}

@media (max-width: 640px) {
  .header {
    height: 50px;
  }
  .header img {
    width: 30px;
    height: 30px;
  }
  .header h1 {
    font-size: 24px;
  }
  .fas {
    font-size: 24px;
    top: 13px;
    right: 20px;
  }
  .linkButton {
    top: 50px;
    flex-direction: column;
  }
  .links {
    width: 100vw;
    font-size: 24px;
  }
  .links:not(:last-of-type) {
    border-bottom: solid 1px #fff;
  }
}