.library-icons {
  padding: 20px 0;
}
.iconItems {
  padding: 0;
  display: flex;
  flex-wrap: wrap;
}
.iconItems li {
  list-style: none;
}
.iconItem {
  width: 16%;
  padding: 15px;
}
.iconItem img {
  width: 100%;
  cursor: pointer;
}
.iconItem.active img {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  max-width:80vw;
  max-height: 80vh;
  object-fit: contain;
  z-index: 4;
}
.back-button {
  text-align: center;
}
.back-button a {
  background: rgb(140, 215, 250);
  text-decoration: none;
  color: #fff;
  font-size: 20px;
  padding: 10px;
  border-radius: 4px;
}
.back-button a:hover {
  opacity: 0.8;
}
.mask {
  transition-duration: 0.3s;
  opacity: 0;
}
.mask.active {
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 3;
}

@media (max-width: 640px) {
  .iconItem {
    width: 25%;
  }
}